body {
  min-width: 1280px;
  background: #eaecef;
}
.card-box {
  padding-top: 18px;
}
.card {
  margin: 12px 50px;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 10px 0 #e2e5eb;
  padding: 20px 50px;
}
.tip {
  min-width: 120px;
  min-height: 50px;
  background: #fff;
  box-shadow: 0 2px 10px 0 rgba(35, 51, 80, 0.1);
  border-radius: 8px;
  color: #333;
  font-size: 16px;
  padding: 8px 10px;
  text-align: center;
  line-height: 26px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.tip:after {
  content: '';
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 5px 5px 5px;
  border-color: transparent transparent #fff transparent;
  position: absolute;
  top: -5px;
}
.input-wrapper {
  width: 360px;
  height: 40px;
  background: #f4f5f7;
  border: 1px solid #eaecef;
  border-radius: 8px;
  display: flex;
  align-items: center;
  margin-right: 20px;
}
.input-wrapper .input-name {
  font-weight: 600;
  display: inline-block;
  width: 80px;
  text-align: center;
}
.input-wrapper input {
  flex: 1;
  outline: none;
  border: none;
  background: transparent;
  margin-right: 20px;
}
.input-wrapper input::placeholder {
  color: #b4b9c3;
}
.input-wrapper.disabled {
  cursor: not-allowed;
}
.input-wrapper.disabled .input-name {
  color: #b4b9c3;
}
.input-wrapper.disabled input {
  display: none;
}
.checkbox-wrapper {
  display: flex;
  align-items: center;
  position: relative;
  cursor: pointer;
}
.checkbox-wrapper input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}
.checkbox-wrapper .checkbox-inner {
  width: 14px;
  height: 14px;
  border-radius: 14px;
  border: 1px solid #eaecef;
  background: #f2f2f2;
  position: relative;
  margin-right: 12px;
}
.checkbox-wrapper .checkbox-inner:after {
  content: '';
  position: absolute;
  width: 6px;
  height: 6px;
  background: #6dc1f5;
  border-radius: 6px;
  left: 3px;
  top: 3px;
  opacity: 0;
  transition: all 0.3s ease-in-out;
  transform: scale(0);
}
.checkbox-wrapper.checked .checkbox-inner:after {
  opacity: 1;
  transition: all 0.3s ease-in-out;
  transform: scale(1);
}
.head-nav {
  width: 100%;
  height: 80px;
  background: #333;
}
.head-nav .cdp-logo {
  float: left;
}
.head-nav .nav-list {
  float: left;
  height: 80px;
  cursor: pointer;
}
.head-nav .nav-list .nav-item {
  float: left;
  font-size: 16px;
  line-height: 80px;
  text-align: center;
  color: #c1c1c1;
  padding: 0 30px;
  transition: all 0.3s ease-in-out;
}
.head-nav .nav-list .nav-item.active,
.head-nav .nav-list .nav-item:hover {
  color: #fff;
}
.head-nav .login {
  float: right;
  line-height: 80px;
  margin-right: 50px;
  position: relative;
  color: #c1c1c1;
  transition: all 0.3s ease-in-out;
}
.head-nav .login:hover {
  color: #fff;
}
.head-nav .login:hover .logout {
  visibility: visible;
  opacity: 1;
}
.head-nav .login .login-name {
  cursor: pointer;
}
.head-nav .login .logout {
  position: absolute;
  left: 50%;
  margin-left: -60px;
  top: 65px;
  cursor: pointer;
  visibility: hidden;
  opacity: 0;
  transition: all 0.3s ease-in-out;
}
.control {
  position: relative;
}
.control .ctr-bar {
  height: 30px;
}
.control .ctr-bar .ctr-bar-item {
  float: left;
  line-height: 30px;
  padding: 0 10px;
  margin-right: 50px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.control .ctr-bar .ctr-bar-item.active {
  font-size: 16px;
  font-weight: 600;
}
.control .ctr-input-wrapper {
  display: flex;
  margin-top: 25px;
}
.control .ctr-input-wrapper .program {
  width: 500px;
}
.control .ctr-input-wrapper .onlyme {
  margin-left: 25px;
}
.content .content-hint {
  color: #b4b9c3;
  margin-bottom: 20px;
}
.cdp-table-wrapper {
  position: relative;
  width: 100%;
  max-height: 360px;
  overflow: hidden;
}
.cdp-table {
  width: inherit;
  overflow: hidden;
  color: #666;
  background: #fff;
}
.cdp-table table {
  width: 100%;
}
.cdp-table th,
.cdp-table td {
  height: 39px;
  min-width: 0;
  text-align: center;
  text-overflow: ellipsis;
  vertical-align: middle;
  border-bottom: 1px solid #e6f0fc;
}
.cdp-table th {
  white-space: nowrap;
  overflow: hidden;
  background: #ebf3fd;
  color: #333;
}
.cdp-table .cdp-table-body {
  height: 320px;
  overflow-x: hidden;
  overflow-y: auto;
}
.footer {
  text-align: center;
  color: #b4b9c3;
  margin-top: 40px;
}
.footer a {
  color: #b4b9c3;
}
.footer a:hover {
  text-decoration: underline;
}
